import { Injectable, inject, Type } from '@angular/core';
import { DetailsUrlService } from './entity-details-url.service';
import { EntityDetailsStateService } from './entity-details.state.service';
import { BaseEntity } from '../../../../shared/models';

@Injectable({ providedIn: 'root' })
export class EntityDetailsService {
  private readonly stateService = inject(EntityDetailsStateService);
  private readonly urlService = inject(DetailsUrlService);

  // Expose signals from state service
  readonly showPanel = this.stateService.showPanel;
  readonly selectedItem = this.stateService.selectedItem;
  readonly detailsComponent = this.stateService.detailsComponent;
  readonly detailsInputs = this.stateService.detailsInputs;
  readonly isLoading = this.stateService.isLoading;
  readonly isSoftRefreshing = this.stateService.isSoftRefreshing;

  constructor() {
    // Initialize URL handling
    this.urlService.initialize();
  }

  /**
   * Open the details panel with the specified item and component
   */
  openDetailsPanel(
    item: BaseEntity,
    componentType: Type<unknown> | null,
  ): void {
    this.stateService.openDetailsPanel(item, componentType);
    this.urlService.updateUrlForDetails(item).catch(() => {});
  }

  /**
   * Close the details panel and clean up URL
   */
  closeDetailsPanel(): void {
    this.urlService.closeDetailsPanel();
  }

  /**
   * Get display title for the currently selected item
   */
  getSelectedItemTitle(): string {
    return this.stateService.getSelectedItemTitle();
  }

  /**
   * Get the type of the currently selected item
   */
  getSelectedItemType(): string {
    return this.stateService.getSelectedItemType();
  }

  /**
   * Get the ID of the currently selected item
   */
  getSelectedItemId(): string {
    return this.stateService.getSelectedItemId();
  }

  /**
   * Soft refresh the currently selected item without affecting accordion states
   * This method preserves UI state while updating entity data
   */
  softRefreshSelectedItem(updatedItem?: BaseEntity): void {
    // The state service now handles its own lifecycle completely
    this.stateService.softRefreshSelectedItem(updatedItem);
  }

  /**
   * Synchronize details panel state with URL query parameters
   * Call this when URL might be out of sync with panel state
   */
  syncDetailsStateWithUrl(): void {
    this.urlService.syncDetailsStateWithUrl();
  }
}
