import { Injectable, inject, DestroyRef, signal } from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { debounceTime, filter, Subject } from 'rxjs';

import { isDefined } from '../../../../shared/utils';
import { GenericDetailsContainerComponent } from '../../components/generic-details-container/generic-details-container.component';
import { BroadcastEventMessageService } from '../broadcast-event-messages.service';
import { EntityDetailsLoaderService } from './entity-details-loader.service';
import { EntityDetailsStateService } from './entity-details.state.service';
import { EntityUrlParserService } from './entity-url-parser.service';
import { EntityUrlManagementService } from './entity-url-management.service';
import { BaseEntity } from '../../../../shared/models';

@Injectable({ providedIn: 'root' })
export class DetailsUrlService {
  private readonly router = inject(Router);
  private readonly activatedRoute = inject(ActivatedRoute);
  private readonly stateService = inject(EntityDetailsStateService);
  private readonly loaderService = inject(EntityDetailsLoaderService);
  private readonly broadcastService = inject(BroadcastEventMessageService);
  private readonly destroyRef = inject(DestroyRef);

  // Inject our new focused services
  private readonly urlParser = inject(EntityUrlParserService);
  private readonly urlManager = inject(EntityUrlManagementService);

  private readonly urlCheckSubject = new Subject<void>();
  private readonly currentGridType = signal<string | null>(null);
  private readonly currentRoute = signal<string>('');
  private readonly initialized = signal<boolean>(false);

  private config = {
    paramNames: {
      id: 'details',
      type: 'type',
    },
    processingDelay: 100,
  };

  constructor() {
    // Listen for entity deletions to clean up the URL parameters
    this.broadcastService
      .on('entity-deleted')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        this.urlManager.cleanUpUrlParams();
      });
  }

  /**
   * Configure all related services
   * @param options - Partial configuration object
   * @example
   * service.configure({
   *  paramNames: {
   *   id: 'details',
   *   type: 'type',
   *  },
   *  processingDelay: 100,
   * });
   *
   * @returns void
   */
  configure(options: Partial<typeof this.config>): void {
    this.config = { ...this.config, ...options };

    // Pass configuration to other services
    this.urlManager.configure({ paramNames: this.config.paramNames });
    this.urlParser.configure({ routeDepth: 3 });
  }

  /**
   * Initialize URL handling and listeners
   */
  initialize(): void {
    // Setup debounced URL checking
    this.urlCheckSubject
      .pipe(
        debounceTime(this.config.processingDelay),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe(() => {
        this.processUrlCheck();
      });

    // Initialize signal values
    this.currentRoute.set(this.urlParser.getBaseRoute(this.router.url));
    this.currentGridType.set(this.urlParser.getGridTypeFromUrl());
    this.initialized.set(true);

    // Check for URL parameters on initial load
    this.checkUrlForDetailsPanel();

    // Set up listeners
    this.setupBroadcastListener();
    this.setupRouteListeners();

    // Explicitly check URL parameters on first navigation
    if (this.router.url.includes('?')) {
      this.checkUrlForDetailsPanel();
    }
  }

  /**
   * Set up broadcast listener for grid selection
   */
  private setupBroadcastListener(): void {
    this.broadcastService
      .on('grid-item-selected')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((data) => {
        // First load the full entity details
        if (
          isDefined(data.item) &&
          isDefined(data.item.id) &&
          isDefined(data.item.entityType)
        ) {
          // Set loading state
          this.stateService.isLoading.set(true);

          // Load full entity details
          this.loaderService.loadEntityDetails(
            String(data.item.entityType),
            String(data.item.id),
          );
        } else {
          // If we don't have the necessary data, just open with what we have
          this.stateService.openDetailsPanel(
            data.item,
            data.componentType ?? GenericDetailsContainerComponent,
          );
        }

        // Update URL using the specialized service
        this.urlManager.updateUrlForDetails(data.item).catch(() => {});
      });

    this.broadcastService
      .on('tree-item-selected')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((data) => {
        // First load the full entity details
        if (
          isDefined(data.item) &&
          isDefined(data.item.id) &&
          isDefined(data.item.entityType)
        ) {
          // Set loading state
          this.stateService.isLoading.set(true);

          // Load full entity details
          this.loaderService.loadEntityDetails(
            String(data.item.entityType),
            String(data.item.id),
          );
        } else {
          // If we don't have the necessary data, just open with what we have
          this.stateService.openDetailsPanel(
            data.item,
            data.componentType ?? GenericDetailsContainerComponent,
          );
        }

        // Update URL using the specialized service
        this.urlManager.updateUrlForDetails(data.item).catch(() => {});
      });
  }

  /**
   * Set up route change listeners
   */
  private setupRouteListeners(): void {
    let previousUrl = this.router.url;

    this.router.events
      .pipe(
        filter((event) => event instanceof NavigationEnd),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((event) => {
        const currentUrl = event.url;

        // Handle route change
        this.handleRouteChange(previousUrl, currentUrl);

        // Check for grid type changes
        const currentGridType = this.urlParser.getGridTypeFromUrl();

        // Close panel if grid type changed and not in same parent route
        if (
          this.currentGridType() !== null &&
          this.currentGridType() !== currentGridType &&
          !this.urlParser.isSameParentRoute(
            this.currentRoute(),
            this.router.url,
          ) &&
          this.stateService.showPanel()
        ) {
          this.closeDetailsPanel();
        }

        // Update current grid type and route
        this.currentGridType.set(currentGridType);
        this.currentRoute.set(this.urlParser.getBaseRoute(this.router.url));

        // Check URL for details panel
        this.checkUrlForDetailsPanel();

        previousUrl = currentUrl;
      });
  }

  /**
   * Trigger a check of URL parameters for the details panel
   */
  private checkUrlForDetailsPanel(): void {
    this.urlCheckSubject.next();
  }

  /**
   * Process the URL check after debouncing
   */
  private processUrlCheck(): void {
    try {
      const queryParams = this.activatedRoute.snapshot.queryParams;
      const entityId = queryParams[this.config.paramNames.id];
      const entityType = queryParams[this.config.paramNames.type];

      if (isDefined(entityId) && isDefined(entityType)) {
        this.loaderService.loadEntityDetails(entityType, entityId);
      } else if (this.stateService.showPanel()) {
        if (this.initialized()) {
          this.closeDetailsPanel();
        }
      }
    } catch (error) {
      console.error('Error checking URL for details panel:', error);
    }
  }

  /**
   * Update URL with details panel state
   */
  async updateUrlForDetails(item: BaseEntity): Promise<void> {
    return this.urlManager.updateUrlForDetails(item);
  }

  /**
   * Close details panel and clean up URL
   */
  closeDetailsPanel(): void {
    if (!this.stateService.showPanel()) return;

    // Clean up URL params using the URL management service
    this.urlManager.cleanUpUrlParams();

    // Close the panel UI
    this.stateService.closeDetailsPanel();

    // Broadcast that the details panel has been closed
    this.broadcastService.broadcast('details-panel-closed', {
      reason: 'panel closed',
    });
  }

  /**
   * Handle route change while preserving details state
   */
  handleRouteChange(previousUrl: string, currentUrl: string): void {
    const previousBase = this.urlParser.getBaseRoute(previousUrl);
    const currentBase = this.urlParser.getBaseRoute(currentUrl);

    // If we're navigating within the same base route, preserve details state
    if (previousBase === currentBase) {
      // Don't close details panel, just check for details params
      this.checkUrlForDetailsPanel();
    } else {
      // If we're navigating to a different section, close the panel
      this.closeDetailsPanel();
    }
  }

  /**
   * Synchronize details panel state with URL query parameters
   */
  syncDetailsStateWithUrl(): void {
    const currentUrl = this.router.url;
    const item = this.stateService.selectedItem();

    if (this.stateService.showPanel() && item) {
      // Panel is open, ensure URL has the correct parameters
      if (!currentUrl.includes('details=') || !currentUrl.includes('type=')) {
        this.updateUrlForDetails(item).catch(() => {});
      }
    } else if (!this.stateService.showPanel()) {
      // Panel is closed, ensure URL doesn't have details parameters
      if (currentUrl.includes('details=') || currentUrl.includes('type=')) {
        this.closeDetailsPanel();
      }
    }
  }
}
